<template xmlns:th="http://www.w3.org/1999/xhtml">
  <div>
    <div class=" header fixed-top" th:insert="fragments/header :: header/">/</div>
    <div class="text-center all main-section">
      <b-container th:if="${#lists.isEmpty(offers)}">
        <h1 class="display-4">Znajdź swoje miejsce</h1>
        <div class="search-box mx-auto text-center w-75">
          <b-form v-on:submit="searchOffers" action="javascript:void(0);e">
            <b-form-group id="citySearch" horizontal label="Miasto" label-for="cityInput">
              <b-form-input id="cityInput" class="col-xs-2" v-model="searchQuery" type="text" size="lg"
                            placeholder="Wpisz miasto"
                            required></b-form-input>
            </b-form-group>
            <div class="h-40 d-inline-block"></div>
            <div class="text-right">
              <b-button type="submit" size="lg" class="btn-outline-dark button">Szukaj</b-button>
            </div>
          </b-form>
        </div>
      </b-container>
      <!--<b-container th:unless="${#lists.isEmpty(offers)}">-->
      <!--<div th:insert="fragments/recyclerview :: recyclerview"></div>-->
      <!--</b-container>-->
    </div>
    <div th:insert="fragments/footer :: footer"></div>
  </div>
</template>

<script>
/* eslint-disable */
  export default {
    name: 'Search',
    data() {
      return {
        msg: 'dupa'
      }
    }
  }
</script>



