import Vue from 'vue'
import App from './pages/greeting/App.vue'
import BootstrapVue from 'bootstrap-vue'
import router from './router/router'

Vue.config.productionTip = false

Vue.use(BootstrapVue)

/* eslint-disable */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})



